.lmj-plant-item-cover {
  height: 250px;
  width: 250px;
  border-radius: 20px;
  object-fit: cover;
}

.lmj-plant-item {
  margin: 10px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  text-transform: capitalize;
}

.lmj-price {
  position: relative;
  width: 50px;
  left: 180px;
  top: 60px;
  color: white;
  padding: 10px;
  font-weight: 600;
  font-size: 18px;
  background-color: #31b572;
}
